      <pre
        class="ne-codeblock language-cpp"
        data-language="cpp"
        id="rqSS0"
      ><span style="font-weight:bold;">4.3</span> 
 <code>#include&lt;iostream&gt;
#include&lt;ctime&gt;
using namespace std;

int main (  )
{
    srand(time(NULL));						//
    int magic=rand()%10+1;					//1-10
    int guess;
    do
    {
        cin&gt;&gt;guess;
        if (guess &gt; magic)
            cout&lt;&lt;"\n";
        else if (guess &lt; magic)
            cout&lt;&lt;"\n";
    }while (guess != magic);
    cout&lt;&lt;""&lt;&lt;guess&lt;&lt;endl;
}</code></pre>
