 <code>#include&lt;iostream&gt;
using namespace std;

long long factn(int n)				//
{
    long long fac;
    if (n == 1)						//
        return 1;
    fac = n * factn(n-1);			//
    return fac;
}

int main (  )
{
    int n;
    cin&gt;&gt;n;
    cout&lt;&lt;factn(n)&lt;&lt;endl;
}</code></pre>
