      ><span style="font-weight:bold;">4.17</span> 
 <code>int place(int m,int n,int start)		//start
{
    if(n==1){ return 1; }
    int sum = 0;
    for(int j=start; j&lt;=m/n; j++)
        sum+=place(m-j,n-1,j);			//
    return sum;
}

int main ()
{
    int m,n;
    cin&gt;&gt;m&gt;&gt;n;
    int sum=0;
    for(int i=1; i&lt;=min(m,n); i++)		//
        sum+=place(m,i,1);				//1
    cout&lt;&lt;sum&lt;&lt;endl;
    return 0;
}</code></pre>
