 <code>#include&lt;iostream&gt;
using namespace std;

int multi(int x, int y) {
    int result = 0;
    while (y) {
        if (y &amp; 1)
            result += x;
        x &lt;&lt;= 1;
        y &gt;&gt;= 1;
    }
    return result;
}
int main (){
    cout&lt;&lt;multi(3,11)&lt;&lt;endl;
}</code></pre>
