        id="tY9kI"
      ><span style="font-weight:bold;">4.29</span> x^n
 <code>double pow(double x, int n) {
    double result = 1;
    int minus = 1;

    if (n &lt; 0) {
        minus = -1;
        n = -n;
    }

    if (0 == n) {
        return 1;
    } else if (0 == x) {
        return 0;
    }

    while (n) {
        if (n &amp; 1) 
            result *= x;
        x *= x;
        n &gt;&gt;= 1;
    }

    return minus&lt;0?1.0/result:result;
}</code></pre>
