        data-language="cpp"
        id="pvzfo"
      ><span style="font-weight:bold;">4.32</span> 
 <code>#include &lt;iostream&gt;
#include &lt;string.h&gt;
#include&lt;sstream&gt;
using namespace std;
int main() {
	string line;
	while(getline(cin,line)){
	    istringstream iss(line);
        int i=0,num,sum=0;
        for(;iss&gt;&gt;num;++i)
            sum+=num;
        cout&lt;&lt;sum/i&lt;&lt;endl;
	}
}</code></pre>
