    cin &gt;&gt; n1 &gt;&gt; n2;
    long long a = 4 - n1%4,b = n2 %4;
    for (long long int i = n1; i &lt; n1 + a; i++)
        sum = i ^ sum;		//
    for (long long int i = n2 - b; i &lt;= n2; i++)
