 <code>#include&lt;iostream&gt;
using namespace std;
int main()
{
    long long n1,n2,sum=0;
    cin &gt;&gt; n1 &gt;&gt; n2;
    if(n1&amp;1)
        sum^=n1++;
    if(n2&amp;1){
        sum ^=((n2+1-n1)&gt;&gt;1)&amp;1;			//((n2+1-n1)/2)%2
    }else{
        sum ^=((n2-n1)&gt;&gt;1)&amp;1;			//((n2-n1)/2)%2
        sum ^=n2;
    }
    cout &lt;&lt; sum &lt;&lt; endl;
    return 0;
}</code></pre>
