int main()
{
    long long n1,n2,sum=0;
    cin &gt;&gt; n1 &gt;&gt; n2;
    bool odd1 = n1&amp;1;
    sum^=n1*odd1;
    n1+=odd1;
    sum ^=((n2+1-n1)&gt;&gt;1)&amp;1;
    sum ^=n2*!(n2&amp;1);
    cout &lt;&lt; sum &lt;&lt; endl;
    return 0;
}</code></pre>
